* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: #ccc;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-items: center;
  border: 1px solid gray;
  border-radius: 6px;
  border-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card img {
  max-height: 170px;
}
